import axios from "axios";
import {router} from "../../router";
import store from "../store";
import localProduct from "./localProduct";

const payment = {
  namespaced: true,
  state:{
    installments: null,
    paymentCard: {
      cardHolderName: "John Doe",
      cardNumber: "5528790000000008",
      expireMonth: 12,
      expireYear: 2030,
      cvc: 123,
      registerCard: 0,
      force3ds: 0,
    },
    paymentList: [],
    paymentDetailModalData: null,
    subscriptions: null,
    license: null,
  },
  getters:{
    getInstallments(state, rootState){
      console.debug("payment/getInstallments");
      return state.installments;
    },
    getPaymentCard(state, rootState){
      console.debug("payment/getPaymentCard");
      return state.paymentCard;
    },
    getPaymentList(state, rootState){
      console.debug("api/getters/getPaymentList");
      return state.paymentList !== undefined ? state.paymentList : false;
    },
    getPaymentDetailModalData(state, rootState){
      console.debug("api/getters/getPaymentDetailModalData");
      return state.paymentDetailModalData !== undefined ? state.paymentDetailModalData : false;
    },
    getSubscription(state, rootState){
      console.debug("payment/getSubscription");
      return state.subscriptions;
    },
    getLicense(state, rootState){
      console.debug("payment/getLicense");
      return state.license;
    },
  },
  mutations:{
    setStateInstallments(state, data) {
      console.debug("payment/setStateInstallments");
      if(data !== undefined) {
        state.installments = data;
        state.paymentCard.force3ds = data===null ? 0 : data.force3ds;
      }
    },
    setStatePaymentList(state, data) {
      console.debug("payment/setStatePaymentList");
      if(data !== undefined) state.paymentList = data;
    },
    setPaymentDetailModalData(state, data) {
      console.debug("payment/setPaymentDetailModalData");
      if(data !== undefined) state.paymentDetailModalData = data;
    },
    setStateSubscription(state, data) {
      console.debug("payment/setStateSubscription");
      if(data !== undefined && data !== null) {
        state.subscriptions = data;
        var productsTmp = store.getters['localProduct/getProducts'];
        state.subscriptions.plan = data.createdSubscriptionData.pricingPlanReferenceCode == productsTmp[1].pricingPlanReferenceCode ? productsTmp[1].plan : localProduct.state.products[2].plan;
      }
    },
    setLicense(state, data) {
      console.debug("payment/setLicense");
      if(data !== undefined && data !== null) {
        state.license = data;
      }
    },
  },
  actions:{
    getInstallment({commit, dispatch, state, rootState}, params){
      console.debug("getInstallment");
      axios.post('payment/installment', {
        binNumber:params.binNumber.substring(0, 6),
        price: rootState.localProduct.product.price
      }).then(response => {
        if(response.data.success){
          if(response.data.result.status === "success"){
            commit("setStateInstallments", response.data.result.installmentDetails[0]);
            return true;
          }
        }
      }).catch(function (error) {
        commit("setStateInstallments", null);
      });
    },
    setPayment({commit, dispatch, state, rootState}, paymentCard){
      console.debug("setPayment");

      var totalPrice = paymentCard.totalPrice;
      var param = {
        price: totalPrice,
        paidPrice: totalPrice,
        installment: paymentCard.installment,
        paymentCard: paymentCard,
        basketItems: [
          {
            id: rootState.localProduct.product.id,
            name: rootState.localProduct.product.title,
            category1: rootState.localProduct.product.category,
            category2: rootState.localProduct.product.category,
            planDay: rootState.localProduct.product.planDay,
            itemType: "VIRTUAL",
            price: totalPrice,
          }
        ]
      };

      var form = $(paymentCard.element.srcElement);
      var bodyBlock = form.closest(".kt-portlet__body");
      var btn = form.find(':submit');

      form.validate({
        // define validation rules
        rules: {
          //= Client Information(step 3)
          // Billing Information
          billing_card_name: {
            required: true
          },
          billing_card_number: {
            required: true,
            creditcard: true
          },
          billing_card_exp_month: {
            required: true
          },
          billing_card_exp_year: {
            required: true
          },
          billing_card_cvv: {
            required: true,
            minlength: 2,
            maxlength: 3
          },
        },
        //display error alert on form submit
        invalidHandler: function(event, validator) {
          swal.fire({
            "title": "",
            "text": lang('PAYMENT.FORM_VALIDATE'),
            "type": "error",
            "confirmButtonClass": "btn btn-secondary",
            "onClose": function(e) {

            }
          });
          event.preventDefault();
        },
        submitHandler: function (form) {
          return false;
        }
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(bodyBlock, lang('PAYMENT.ACTION'));

      if(paymentCard.force3ds){
        axios.post('payment/threedspay', param).then(response => {
          if(response.data.success){
            form.clearForm();
            form.validate().resetForm();
            commit("setStateInstallments", null);
            FUnloading(btn[0]);
            FUnBlockLoading(bodyBlock);

            if(response.data.result.status === "success"){
              var customModal = $('#3dModal');
              customModal.find('.modal-body').html(response.data.result.htmlContent);
              customModal.modal('show');
            }else{
              Fmessage(null, response.data.result.errorMessage, "error");
            }

            return true;
          }
        }).catch(function (error) {
          axiosCatchMessage(error);
        }).then(function () {
          FUnloading(btn[0]);
          FUnBlockLoading(bodyBlock);
        });
      }else{
        axios.post('payment/payment', param).then(response => {
          if(response.data.success){
            form.clearForm();
            form.validate().resetForm();
            Fmessage(null, response.data.message, "success");
            commit("setStateInstallments", null);
            FUnloading(btn[0]);
            FUnBlockLoading(bodyBlock);
            router.push("/Buy/PaymentList");
            return true;
          }
        }).catch(function (error) {
          axiosCatchMessage(error);
        }).then(function () {
          FUnloading(btn[0]);
          FUnBlockLoading(bodyBlock);
        });
      }



    },
    getPaymentList({commit, getters, rootState}) {
      FBlockLoading($('.kt_wrapper'), null);
      axios.get('payment/paymentList').then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setStatePaymentList", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('.kt_wrapper'), null);
      });
    },
    getSubscription({commit, dispatch, getters}) {
      console.debug("payment/getSubscription");
      if(store.getters.isAuthenticated){
        axios.get('payment/subscriptionInfo').then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                commit("setStateSubscription", response.data.result);
              }
            }
          }
        ).catch(function (error) {

        }).then(function () {
          commit("setStateSubscription", null);
        });
      }
    },
    getLicense({commit, dispatch, getters}) {
      console.debug("payment/getLicense");
      if(store.getters.isAuthenticated){
        axios.get('payment/license').then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                commit("setLicense", response.data.result);
              }
            }
          }
        ).catch(function (error) {

        }).then(function () {
          commit("setLicense", null);
        });
      }
    },
    setSubscription({commit, dispatch, state, rootState}, paymentCard){
      console.debug("setSubscription");

      var param = {
        price: paymentCard.totalPrice,
        pricingPlanReferenceCode: rootState.localProduct.product.pricingPlanReferenceCode,
        planDay: rootState.localProduct.product.planDay,
        paymentCard: paymentCard,
      };

      var form = $(paymentCard.element.srcElement);
      var bodyBlock = form.closest(".kt-portlet__body");
      var btn = form.find(':submit');

      form.validate({
        // define validation rules
        rules: {
          //= Client Information(step 3)
          // Billing Information
          billing_card_name: {
            required: true
          },
          billing_card_number: {
            required: true,
            creditcard: true
          },
          billing_card_exp_month: {
            required: true
          },
          billing_card_exp_year: {
            required: true
          },
          billing_card_cvv: {
            required: true,
            minlength: 2,
            maxlength: 3
          },
        },
        //display error alert on form submit
        invalidHandler: function(event, validator) {
          swal.fire({
            "title": "",
            "text": lang('PAYMENT.FORM_VALIDATE'),
            "type": "error",
            "confirmButtonClass": "btn btn-secondary",
            "onClose": function(e) {

            }
          });
          event.preventDefault();
        },
        submitHandler: function (form) {
          return false;
        }
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(bodyBlock, lang('PAYMENT.ACTION'));

      axios.post('payment/subscription', param).then(response => {
        if(response.data.success){
          form.clearForm();
          form.validate().resetForm();
          Fmessage(null, response.data.message, "success");
          commit("setStateInstallments", null);
          FUnloading(btn[0]);
          FUnBlockLoading(bodyBlock);
          dispatch("getSubscription");
          router.push("/Buy/PaymentList");
          return true;
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(btn[0]);
        FUnBlockLoading(bodyBlock);
      });

    },
    cancelSubscription({commit, dispatch, getters}) {
      console.debug("payment/cancelSubscription");
      if(store.getters.isAuthenticated){
        axios.get('payment/subscriptionCancel').then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                Fmessage(null, response.data.message, "success");
                dispatch("getSubscription");
              }
            }
          }
        ).catch(function (error) {
          axiosCatchMessage(error);
        });
      }
    },
    retrySubscription({commit, dispatch, getters}, params) {
      console.debug("payment/retrySubscription");
      if(store.getters.isAuthenticated){
        axios.post('payment/subscriptionRetry', params).then(response => {
          if (response.data.success) {
            if(response.data.result !== undefined) {
              Fmessage(null, response.data.message, "success");
            }
          }
        }).catch(function (error) {
          axiosCatchMessage(error);
        }).then(function () {
        });
      }
    },
  },
};

export default payment



