import Vue from "vue";
import VueRouter from "vue-router";
import store from "./store/store";

import Dashboard from "./pages/Home/Dashboard";
import UserList from "./pages/User/UserList";
import Profile from "./pages/User/Profile";
import Address from "./pages/User/Address";
import Buy from "./pages/Buy/Buy";
import BuyProfile from "./pages/Buy/Profile";
import Payment from "./pages/Buy/Payment";
import PaymentList from "./pages/Buy/PaymentList";
import Auth from "./pages/Login/Auth";
import Api from "./pages/Api/ApiList";
import ApiForm from "./pages/Api/ApiForm";
import ApiProfile from "./pages/Api/ApiProfile";
import Platform from "./pages/Api/Platform";
import ProductList from "./pages/Product/ProductList";
import ProductForm from "./pages/Product/ProductForm";
import Order from "./pages/Order/OrderList";
import DesignInvoiceForm from "./pages/Settings/Design/Invoice/DesignInvoiceForm";
import DesignInvoiceList from "./pages/Settings/Design/Invoice/DesignInvoiceList";

Vue.use(VueRouter);

const routes = [
  { path : "/Login", component: Auth},
  { path : "/Register", component: Auth},
  { path : "/", component : Dashboard, name : "Dashboard", beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "*", redirect: "/", beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Dashboard", component : Dashboard, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/User", component: UserList, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/User/Profile", component: Profile, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/User/Address", component: Address, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Buy", component: Buy, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Buy/Payment/:id", component: Payment, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Buy/Profile/:id", component: BuyProfile, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Buy/PaymentList", component: PaymentList, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Api", component: Api, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Api/Add/:id", component: Platform, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Api/Detail/:id", component: ApiForm, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Api/Profile/:id", component: ApiProfile, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Product", component: ProductList, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Product/Detail/:id", component: ProductForm, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Product/Form", component: ProductForm, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Order", component: Order, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Settings/Design/Invoice/Form", component: DesignInvoiceForm, beforeEnter(to, from, next){authCheck(to, from, next);}},
  { path : "/Settings/Design/Invoice/List", component: DesignInvoiceList, beforeEnter(to, from, next){authCheck(to, from, next);}},
];

export const router = new VueRouter({
  //mode: "history",
  routes,
  pageLoad: false
});

function authCheck(to, from, next) {
  init();
  console.debug("authCheck");
  if (!router.pageLoad){
    store.dispatch("user/initAuth");
    router.pageLoad = true;
  }
  if(store.getters.isAuthenticated){
    next(); // Geçişe izin ver
  }else{
    next("/Login");
  }
}



