                <div class="col-6">
                  <label class="form-control-label">CargoCompany1:</label>
                  <input type="text" class="form-control" v-model="liveProduct.detailData.CargoCompany1">
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">İptal</button>
              <button type="submit" class="btn btn-primary">Kaydet</button>
            </div>
          </form>

        </div>
      </div>
    </div>
    <!--end::Modal-->

  </div>
</template>

<script>
    import axios from "axios";
    var tmpProductVue = null;
    export default {
      name: "DesignInvoiceList",
      data(){
        return {
          platformId: 0,
          liveProduct: false,
          tmpLiveProduct: false,
          loadingMsg: "",
          category: {
            n11: {}
          }
        }
      },
      computed: {
        getProducts(){
          return this.$store.getters['product/getProducts'];
        },
      },
      created: function () {
        tmpProductVue = this;
        productListPage.init([], this);
        this.$store.dispatch('product/getProducts');
        FBlockLoading($('.product-list-loading'), null);

        this.$store.watch((state, getters) => this.$store.getters['product/getProducts'],(newValue, oldValue) => {
            FUnBlockLoading($('.product-list-loading'), null);
            if(newValue.length===0) tmpProductVue.loadingMsg = "Ürün bulunamadı!";
          },
        );

      },
      methods: {
        getPlatformIcon(platformId) {
            var platform = this.$store.getters['api/getPlatform'](platformId);
            if(platform.length>0) {
              return platform[0].icon;
            }
            return"";
        },
        onPlatformProductEditModal(platformId, subProductId){
          this.platformId = platformId;
          tmpProductVue = this;

          $('#platformProductEditModal').modal('show');
          $('#platformProductEditModal').on('shown.bs.modal', function (e) {
            FBlockLoading($('#platformProductEditModal .modal-loading'), null);
          });
          $('#platformProductEditModal').on('hidden.bs.modal', function (e) {
            tmpProductVue.platformId = 0;
            tmpProductVue.liveProduct = false;
          });

          console.log("modalEvent");
          this.$store.dispatch("product/getLiveProduct", {subProductId: subProductId});
          tmpProductVue.unwatch = this.$store.watch((state, getters) => this.$store.getters['product/getLiveProduct'],(newValue, oldValue) => {
              tmpProductVue.liveProduct = newValue;
              console.log(newValue);
              FUnBlockLoading($('#platformProductEditModal .modal-loading'), null);
              setTimeout(function(){
                if(tmpProductVue.platformId === 1){
                  tmpProductVue.searchCategoryN11();
                  tmpProductVue.getCategoryN11();
                }else if(tmpProductVue.platformId === 2){
                  tmpProductVue.searchCategoryGG();
                  tmpProductVue.getCategoryGG();
                }else{

                }
                tmpProductVue.unwatch();
              }, 1000);
            },
          );
        },
        onsubmit(element){
          this.$store.dispatch("product/updateProduct", {...this.liveProduct, element: element})
        },
        getCategoryN11(){
          var categoryId = tmpProductVue.liveProduct.detailData.category.id;
          tmpProductVue.tmpLiveProduct = JSON.parse(JSON.stringify(tmpProductVue.liveProduct));
          axios.get('category/n11/getdetail/' + categoryId).then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                tmpProductVue.category.n11 = tmpProductVue.subCategoryNameN11(response.data.result);
                if(tmpProductVue.category.n11.attributes !== undefined) {
                  tmpProductVue.liveProduct.detailData.attributes.attribute = [];
                  for (var i = 0; i < tmpProductVue.category.n11.attributes.length; i++) {
                    var tmpValue = tmpProductVue.tmpLiveProduct.detailData.attributes.attribute[tmpProductVue.objectContainValueN11(tmpProductVue.tmpLiveProduct.detailData.attributes.attribute, tmpProductVue.category.n11.attributes[i].name)];
                    tmpProductVue.liveProduct.detailData.attributes.attribute.push({
                      id: tmpProductVue.category.n11.attributes[i].Id,
                      name: tmpProductVue.category.n11.attributes[i].name,
                      value: tmpValue.value,
                    });
                  }
                }
              }
            }
          }).catch(function (error) {
            axiosCatchMessage(error);
          }).then(function () {
          });
        },
        searchCategoryN11(){
          var typeaheadItemInput = $("#kt_typeahead_n11");
          var typeaheadItem = new Bloodhound({
            datumTokenizer: function (datum) {
              return Bloodhound.tokenizers.whitespace(datum.value);
            },
            queryTokenizer: Bloodhound.tokenizers.whitespace,
            remote: {
              url: 'https://api.iltechsoft.com/category/n11/search?name='+encodeURIComponent('%QUERY')+'&parent=true',
              wildcard: encodeURIComponent('%QUERY'),
              filter: function (items) {
                // Map the remote source JSON array to a JavaScript object array
                return $.map(items.result, function (item) {
                  item["paramText"] = "";
                  if(item.subCategory !== undefined){
                    item = tmpProductVue.subCategoryNameN11(item);
                  }
                  return {
                    key: item.Id,
                    value: item.name,
                    parentText: item.paramText
                  };
                });
              }
            }
          });

          typeaheadItemInput.typeahead({
            hint: !0,
            highlight: !0,
            minLength: 1
          }, {
            name: 'key',
            displayKey: 'value',
            source: typeaheadItem.ttAdapter(),
            limit: 5,
            templates: {
              empty: [
                '<div class="empty-message p-3">',
                'Kategori bulunamadı!',
                '</div>'
              ].join('\n'),
              suggestion: Handlebars.compile('<div class="p-2"><strong>{{value}}</strong><br>{{parentText}}</div>')
            }
          });

          typeaheadItemInput.bind('typeahead:select', function(e, item) {
            tmpProductVue.liveProduct.detailData.category = {
              id: item.key,
              name: item.value,
              fullName: item.parentText
            }
            tmpProductVue.getCategoryN11();
          });
        },
        objectContainValueN11(paramObjects, value){
          if(paramObjects!==undefined){
            for(var i=0; i<paramObjects.length; i++){
              if(paramObjects[i].name === value){
                return i;
              }
            }
          }
          return 0;
        },
        subCategoryNameN11(categoryItem, paramText = "", subCategory = true) {
          var tmpItem = categoryItem;
          paramText += categoryItem.name + (categoryItem.subCategory !== undefined ? " > " : "");
          tmpItem["paramText"] = paramText;
          if(categoryItem.subCategory !== undefined){
            return tmpProductVue.subCategoryNameN11(categoryItem.subCategory, paramText);
          }
          return tmpItem;
        },
        getCategoryGG(){
          var categoryId = tmpProductVue.liveProduct.detailData.product.storeCategoryId;
          axios.get('category/gg/getdetail/' + categoryId).then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                tmpProductVue.category.n11 = response.data.result;
                tmpProductVue.liveProduct.detailData.attributes.attribute = [];
                for(var i=0; i<tmpProductVue.category.n11.attributes.length; i++){
                  tmpProductVue.liveProduct.detailData.attributes.attribute.push({
                    id: tmpProductVue.category.n11.attributes[i].Id,
                    name: tmpProductVue.category.n11.attributes[i].Name,
                    value: "",
                  });
                }
              }
            }
          }).catch(function (error) {
            axiosCatchMessage(error);
          }).then(function () {
          });
        },
        searchCategoryGG(){
          var typeaheadItemInput = $("#kt_typeahead_gg");
          var typeaheadItem = new Bloodhound({
            datumTokenizer: function (datum) {
              return Bloodhound.tokenizers.whitespace(datum.value);
            },
            queryTokenizer: Bloodhound.tokenizers.whitespace,
            remote: {
              url: 'https://api.iltechsoft.com/category/gg/search?name='+encodeURIComponent('%QUERY')+'&parent=true',
              wildcard: encodeURIComponent('%QUERY'),
              filter: function (items) {
                // Map the remote source JSON array to a JavaScript object array
                return $.map(items.result, function (item) {
                  item["paramText"] = "";
                  if(item.subCategory !== undefined){
                    item = tmpProductVue.subCategoryNameN11(item);
                  }
                  return {
                    key: item.Id,
                    value: item.name,
                    parentText: item.paramText
                  };
                });
              }
            }
          });

          typeaheadItemInput.typeahead({
            hint: !0,
            highlight: !0,
            minLength: 1
          }, {
            name: 'key',
            displayKey: 'value',
            source: typeaheadItem.ttAdapter(),
            limit: 5,
            templates: {
              empty: [
                '<div class="empty-message p-3">',
                'Kategori bulunamadı!',
                '</div>'
              ].join('\n'),
              suggestion: Handlebars.compile('<div class="p-2"><strong>{{value}}</strong><br>{{parentText}}</div>')
            }
          });

          typeaheadItemInput.bind('typeahead:select', function(e, item) {
            tmpProductVue.liveProduct.detailData.category = {
              id: item.key,
              name: item.value,
              fullName: item.parentText
            }
            tmpProductVue.getCategoryN11();
          });
        },
        objectContainValueGG(paramObjects, value){
          if(paramObjects!==undefined){
            for(var i=0; i<paramObjects.length; i++){
              if(paramObjects[i].name === value){
                return i;
              }
            }
          }
          return 0;
        },
        subCategoryNameGG(categoryItem, paramText = "", subCategory = true) {
          var tmpItem = categoryItem;
          paramText += categoryItem.name + (categoryItem.subCategory !== undefined ? " > " : "");
          tmpItem["paramText"] = paramText;
          if(categoryItem.subCategory !== undefined){
            return tmpProductVue.subCategoryNameN11(categoryItem.subCategory, paramText);
          }
          return tmpItem;
        }
      }
    }

</script>

<style scoped>

</style>



