<template>
  <div>
    <!-- begin:: Subheader -->
    <div class="kt-subheader   kt-grid__item" id="kt_subheader">
      <div class="kt-container  kt-container--fluid ">
        <div class="kt-subheader__main">
          <h3 class="kt-subheader__title">Api Listesi</h3>
          <span class="kt-subheader__separator kt-hidden"></span>
          <div class="kt-subheader__breadcrumbs">
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-home" active-class="kt-subheader__breadcrumbs-link--active" exact><i class="flaticon2-shelter"></i></router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ana Sayfa</router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/Api" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Api</router-link>
          </div>
        </div>
      </div>
    </div>
    <!-- end:: Subheader -->
    <!-- begin:: Content -->
    <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
      <div class="kt-portlet kt-portlet--mobile">
        <div class="kt-portlet__head kt-portlet__head--lg">
          <div class="kt-portlet__head-label">
            <span class="kt-portlet__head-icon">
              <i class="kt-font-brand flaticon2-line-chart"></i>
            </span>
            <h3 class="kt-portlet__head-title">
              Api Listesi
            </h3>
          </div>
          <div class="kt-portlet__head-toolbar">
            <div class="kt-portlet__head-wrapper">              &nbsp;
              <div class="dropdown dropdown-inline">
                <button type="button" class="btn btn-brand btn-icon-sm" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  <i class="flaticon2-plus"></i> Yeni Ekle
                </button>
                <div class="dropdown-menu dropdown-menu-right">
                  <ul class="kt-nav" v-if="getPlatforms">
                    <li class="kt-nav__section kt-nav__section--first">
                      <span class="kt-nav__section-text">Platform seçiniz:</span>
                    </li>
                    <li class="kt-nav__item" v-for="item in getPlatforms">
                      <router-link tag="a" :to="'/Api/Add/' + item.id" class="kt-nav__link">
                        <i class="kt-nav__link-icon flaticon2-open-text-book"></i>
                        <span class="kt-nav__link-text">{{item.name}}</span>
                      </router-link>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="kt-portlet__body">
          <!--begin: Search Form -->
          <div class="kt-form kt-form--label-right kt-margin-t-20 kt-margin-b-10">
            <div class="row align-items-center">
              <div class="col-xl-8 order-2 order-xl-1">
                <div class="row align-items-center">
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                    <div class="kt-input-icon kt-input-icon--left">
                      <input type="text" class="form-control" placeholder="Search..." id="generalSearch">
                      <span class="kt-input-icon__icon kt-input-icon__icon--left">
																<span><i class="la la-search"></i></span>
															</span>
                    </div>
                  </div>
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                    <div class="kt-form__group kt-form__group--inline">
                      <div class="kt-form__label">
                        <label>Status:</label>
                      </div>
                      <div class="kt-form__control">
                        <select class="form-control bootstrap-select" id="kt_form_status">
                          <option value="">Hepsi</option>
                          <option value="4">Aktif</option>
                          <option value="6">Pasif</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                  </div>
                </div>
              </div>
              <div class="col-xl-4 order-1 order-xl-2 kt-align-right">
                <a href="#" class="btn btn-default kt-hidden">
                  <i class="la la-cart-plus"></i> New Order
                </a>
                <div class="kt-separator kt-separator--border-dashed kt-separator--space-lg d-xl-none"></div>
              </div>
            </div>
          </div>

          <!--end: Search Form -->
        </div>
        <div class="kt-portlet__body kt-portlet__body--fit">

          <!--begin: Datatable -->
          <div class="kt-datatable" id="local_data"></div>

          <!--end: Datatable -->
        </div>
      </div>
    </div>
    <!-- end:: Content -->
  </div>
</template>

<script>
    import apiPage from "../../assets/js/custom/pages/api";
    export default {
      name: "ApiList",
      data(){
        return {}
      },
      computed: {
        getPlatforms(){
          return this.$store.getters['api/getPlatforms'];
        },
      },
      created: function () {
        FBlockLoading($('.kt_wrapper'), null);
        apiPage.init(this);
        this.$store.dispatch('api/getPlatforms');
        this.$store.dispatch('api/getApiList');
        this.unwatch = this.$store.watch((state, getters) => this.$store.getters['api/getApiList'],(newValue, oldValue) => {

            for(var i=0; i<newValue.length; i++){
              var platform = this.$store.getters['api/getPlatform'](newValue[i].platformid);
              newValue[i]["platformName"] = platform[0].name;
              newValue[i]["platformIcon"] = platform[0].icon;
            }

            apiPage.setListData(newValue);
            FUnBlockLoading($('.kt_wrapper'), null);
          },
        );
      },
      beforeDestroy() {
        this.unwatch();
      }
    }
</script>

<style scoped>

</style>



