<script>
  import apiPage from "../../assets/js/custom/pages/api";

  export default {
    name: "Payment",
    computed: {
      product() {
        return this.$store.getters['localProduct/getProduct'](this.$route.params.id);
      },
      paymentInstallmentsInfo() {
        let installments = this.$store.getters['payment/getInstallments'];
        this.totalPrice = installments != null ? installments.price : 0;
        this.paymentCard.force3dsRequired = installments != null ? installments.force3ds : 0;
        this.paymentCard.force3ds = installments != null ? installments.force3ds : 0;
        return installments;
      },
      userDataValidation() {
        console.debug("userDataValidationComputed");
        return this.$store.getters['user/dataValidation'];
      },
      userInfo() {
        var param = this.$store.getters['user/getInfo'];
        var paramTmp = {};
        Object.assign(paramTmp, param);
        return paramTmp;
      },
      addressInfo() {
        console.debug("addressInfoComputed");
        var param = this.$store.getters['user/getAddress'];
        var paramTmp = {};
        Object.assign(paramTmp, param);
        return paramTmp;
      },
    },
    data() {
      return {
        id: this.$route.params.id,
        totalPrice: 0,
        dataUpdate: false,
        paymentCard: {
          cardHolderName: null,
          cardNumber: null,
          expireMonth: null,
          expireYear: null,
          cvc: null,
          registerCard: 1,
          installment: 1,
          force3ds: 0,
          force3dsRequired: 0,
        }
      }
    },
    created: function () {
      console.debug("pagePayment Created");
      this.$store.getters['localProduct/getProduct'](this.$route.params.id);
      this.$store.dispatch("payment/getInstallment", {binNumber: "123456"});
    },
    methods: {
      getInstallment(event) {
        if (event.target.value.length >= 6) {
          this.$store.dispatch("payment/getInstallment", {binNumber: event.target.value})
        }
      },
      installmentSelect(event) {
        this.totalPrice = event.totalPrice;
        this.paymentCard.installment = event.installmentNumber;
      },
      onsubmit(element) {
        if (this.paymentCard.registerCard) {
          this.paymentCard.registerCard = 1;
          this.$store.dispatch("payment/setSubscription", {
            ...this.paymentCard,
            totalPrice: this.totalPrice,
            element: element
          })
        } else {
          this.paymentCard.registerCard = 0;
          this.$store.dispatch("payment/setPayment", {
            ...this.paymentCard,
            totalPrice: this.totalPrice,
            element: element
          })
        }
      },
      onsubmitProfile(element) {
        console.debug("onSubmitProfileEdit");
        var objectParam = {
          "userInfo": this.userInfo,
          "addressInfo": this.addressInfo,
          "element": element
        };
        this.$store.dispatch("user/setInfoAndAddress", objectParam);
      }
    }
  }
</script>

<style scoped>

</style>



