<script>
  import {router} from "../../router";
  import apiPage from "../../assets/js/custom/pages/api";
  var tmpApiForm = null;
  export default {
    name: "ApiProfile",
    data(){
      return {
        apiId: this.$route.params.id,
        api: null,
        platformId: null,
        platform: null,
        formData: {},
        getApiWatch: null,
      }
    },
    computed: {
      getPlatformFields(){
        var param = this.$store.getters['api/getPlatformFields'];
        if(param.length>0 && this.api !== null){
          var tmpObjectForm = {};
          param.push({name:'Durum', column:'active'});
          for(var i=0; i<param.length; i++){
            tmpObjectForm[param[i].column] = this.api[param[i].column];
          }
          this.formData = tmpObjectForm;
          return param;
        }
        return null;
      },
      getPlatform(){
        var param = this.$store.getters['api/getPlatform'](this.platformId);
        return param.length>0 ? param[0] : null;
      },
      getApi(){
        var param = this.$store.getters['api/getApi'];
        if(param!=null){
          if(this.getApiWatch !== null) this.getApiWatch();
          this.platformId = param.platformid;
          this.api = param;
          return param;
        }
        return null;
      },
      getProducts(){
        return this.$store.getters['api/getProducts'];
      },
    },
    created: function () {
      KTApp.initTooltips();
      console.debug("ApiProfile Created");
      tmpApiForm = this;
      this.$store.dispatch("api/getApi", {id: this.$route.params.id});

      this.getApiWatch = this.$store.watch((state, getters) => this.$store.getters['api/getApi'],(newValue, oldValue) => {
        this.$store.dispatch("api/getProducts", {id: tmpApiForm.$route.params.id, apiId: tmpApiForm.apiId});
          tmpApiForm.getApi;
          tmpApiForm.platform = this.$store.getters['api/getPlatform'](newValue.platformid);
          this.$store.dispatch("api/getPlatformFields", {platformId: newValue.platformid});
        },
      );

      this.$store.watch((state, getters) => this.$store.getters['api/getPlatformFields'],(newValue, oldValue) => {
        //tmpApiForm.getPlatformFields;
        //tmpApiForm.getPlatform;
        },
      );

    },
    methods: {
      onsubmit(element){
        this.$store.dispatch("api/updateApi", {...this.formData, id: this.apiId, pid: this.platformId, element: element})
      },
      onApiProductToLocalProduct(){
        this.$store.dispatch("api/setApiProductToLocalProduct", {apiId: this.apiId});
      }
    }
  }
</script>

