<template>
    <div>
      <!-- begin:: Subheader -->
      <div class="kt-subheader   kt-grid__item" id="kt_subheader">
        <div class="kt-container  kt-container--fluid ">
          <div class="kt-subheader__main">
            <h3 class="kt-subheader__title">FATURA DÜZENLE</h3>
            <span class="kt-subheader__separator kt-hidden"></span>
            <div class="kt-subheader__breadcrumbs">
              <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-home" active-class="kt-subheader__breadcrumbs-link--active" exact><i class="flaticon2-shelter"></i></router-link>
              <span class="kt-subheader__breadcrumbs-separator"></span>
              <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ana Sayfa</router-link>
              <span class="kt-subheader__breadcrumbs-separator"></span>
              <router-link tag="a" to="/Settings" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ayarlar</router-link>
              <span class="kt-subheader__breadcrumbs-separator"></span>
              <router-link tag="a" :to="'/Settings/Design/Invoice/Form' + $route.params.id" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Fatura Düzenle</router-link>
            </div>
          </div>
        </div>
      </div>
      <!-- end:: Subheader -->

      <!-- begin:: Content -->
      <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
        <div class="kt-portlet">
          <div class="kt-portlet__body kt-portlet__body--fit">
            <div class="kt-grid kt-wizard-v3 kt-wizard-v3--white" id="kt_wizard_v3" data-ktwizard-state="step-first">
              <div class="kt-grid__item">
                <!--begin: Form Wizard Nav -->
                <div class="kt-wizard-v3__nav">
                  <!--doc: Remove "kt-wizard-v3__nav-items--clickable" class and also set 'clickableSteps: false' in the JS init to disable manually clicking step titles -->
                  <div class="kt-wizard-v3__nav-items kt-wizard-v3__nav-items--clickable">
                    <div class="kt-wizard-v3__nav-item" data-ktwizard-type="step" data-ktwizard-state="current">
                      <div class="kt-wizard-v3__nav-body">
                        <div class="kt-wizard-v3__nav-label">
                          <span>1</span> FATURA TİPİ
                        </div>
                        <div class="kt-wizard-v3__nav-bar"></div>
                      </div>
                    </div>
                    <div class="kt-wizard-v3__nav-item" data-ktwizard-type="step">
                      <div class="kt-wizard-v3__nav-body">
                        <div class="kt-wizard-v3__nav-label">
                          <span>2</span> FATURA YÜKLEME
                        </div>
                        <div class="kt-wizard-v3__nav-bar"></div>
                      </div>
                    </div>
                    <div class="kt-wizard-v3__nav-item" data-ktwizard-type="step">
                      <div class="kt-wizard-v3__nav-body">
                        <div class="kt-wizard-v3__nav-label">
                          <span>3</span> FATURA DÜZENLEME
                        </div>
                        <div class="kt-wizard-v3__nav-bar"></div>
                      </div>
                    </div>
                  </div>
                </div>
                <!--end: Form Wizard Nav -->
              </div>
              <div class="kt-grid__item kt-grid__item--fluid kt-wizard-v3__wrapper">

                <!--begin: Form Wizard Form-->
                <form class="kt-form pt-3" id="kt_form" style="width: 100%;">

                  <!--begin: Form Wizard Step 1-->
                  <div class="kt-wizard-v3__content" data-ktwizard-type="step-content" data-ktwizard-state="current">
                    <div class="kt-container">
                      <div class="row">
                        <div class="form-group form-group-marginless col-12">
                          <div class="row justify-content-md-center">
                            <div class="col col-lg-3">
                              <label class="kt-option">
																<span class="kt-option__control">
																	<span class="kt-radio">
																		<input type="radio" name="m_option_1" value="1" v-model="pageType" @change="pageTypeChangeEvent">
																		<span></span>
																	</span>
																</span>
                                <span class="kt-option__label">
																	<span class="kt-option__head">
																		<span class="kt-option__title">
																			A4
																		</span>
																	</span>
																	<span class="kt-option__body">
                                    <i class="fa fa-file" style="font-size: 40pt;"></i>
																	</span>
																</span>
                              </label>
                            </div>
                            <div class="col col-lg-3">
                              <label class="kt-option">
																<span class="kt-option__control">
																	<span class="kt-radio">
																		<input type="radio" name="m_option_1" value="2" v-model="pageType" @change="pageTypeChangeEvent">
																		<span></span>
																	</span>
																</span>
                                <span class="kt-option__label">
																	<span class="kt-option__head">
																		<span class="kt-option__title">
																			A4 Yatay
																		</span>
																	</span>
																	<span class="kt-option__body">
                                    <i class="fa fa-file" style="font-size: 40pt;"></i>
																	</span>
																</span>
                              </label>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!--end: Form Wizard Step 1-->

                  <!--begin: Form Wizard Step 2-->
                  <div class="kt-wizard-v3__content" data-ktwizard-type="step-content">
                    <div class="kt-container">
                      <div class="row">
                        <div class="col-lg-8">
                          <!--begin::Portlet-->
                          <div class="kt-portlet kt-portlet--bordered">
                            <div class="kt-portlet__head kt-bg-fill-info">
                              <div class="kt-portlet__head-label">
                                <h3 class="kt-portlet__head-title kt-font-inverse-primary">
                                  Fatura
                                </h3>
                              </div>
                            </div>
                            <div class="kt-portlet__body">
                              <div class="row">
                                <div class="col-12" :style="{height: pageHeight, border: '1px solid #000'}">
                                  <!--transform: scale(0.669445);-->
                                  <div id="list" class="invoicePage list" :style="{transform: pageScale, width: pageWidth, height: pageHeight, 'background-color': 'aliceblue', 'background-image': 'url(src/assets/images/fatura-bg.png)'}">
                                    <draggable class="list-group invoice-drop-area" :list="list1" group="invoice" @change="log" @remove="onRemove" @add="onAdd" style="height: 297mm;"></draggable>
                                    <VueDragResize v-for="(rect, index) in rects"
                                                   :key="index"
                                                   :w="rect.width"
                                                   :h="rect.height"
                                                   :x="rect.left"
                                                   :y="rect.top"
                                                   :parentW="listWidth"
                                                   :parentH="listHeight"
                                                   :axis="rect.axis"
                                                   :isActive="rect.active"
                                                   :minw="rect.minw"
                                                   :minh="rect.minh"
                                                   :isDraggable="rect.draggable"
                                                   :isResizable="rect.resizable"
                                                   :parentLimitation="rect.parentLim"
                                                   :snapToGrid="rect.snapToGrid"
                                                   :aspectRatio="rect.aspectRatio"
                                                   :z="rect.zIndex"
                                                   v-on:activated="activateEv(index)"
                                                   v-on:deactivated="deactivateEv(index)"
                                                   v-on:dragging="changePosition($event, index)"
                                                   v-on:resizing="changeSize($event, index)">
                                      <div class="filler" :style="{backgroundColor:rect.color}">{{rect.content}}</div>
                                      <div class="btn btn-danger vdr-btn-delete" v-on:click="removeEv(index)">Sil</div>
                                    </VueDragResize>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>

                        <div class="col-lg-4">
                          <!--begin::Portlet-->
                          <div class="kt-portlet kt-portlet--bordered">
                            <div class="kt-portlet__head kt-bg-fill-info">
                              <div class="kt-portlet__head-label">
                                <h3 class="kt-portlet__head-title kt-font-inverse-primary">
                                  Alanlar
                                </h3>
                              </div>
                            </div>
                            <div class="kt-portlet__body">
                              <div class="form-group">
                                <div class="row">
                                  <div class="col-12">
                                    <h5>Genel Bilgiler</h5>
                                    <draggable class="dragArea list-group" :list="list2" :group="{ name: 'invoice', pull: 'clone', put: false }">
                                      <div class="list-group-item" v-for="(element, index) in list2" :key="element.name">
                                        {{ element.title }}
                                      </div>
                                    </draggable>
                                  </div>
                                  <hr/>
                                  <div class="col-12 mt-4">
                                    <h5>Müşteri Bilgileri</h5>
                                    <draggable class="dragArea list-group" :list="list3" :group="{ name: 'invoice', pull: 'clone', put: false }">
                                      <div class="list-group-item" v-for="(element, index) in list3" :key="element.name">
                                        {{ element.title }}
                                      </div>
                                    </draggable>
                                  </div>
                                  <div class="col-12 mt-4">
                                    <h5>Fatura Bilgileri</h5>
                                    <draggable class="dragArea list-group" :list="list4" :group="{ name: 'invoice', pull: 'clone', put: false }">
                                      <div class="list-group-item" v-for="(element, index) in list4" :key="element.name">
                                        {{ element.title }}
                                      </div>
                                    </draggable>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!--end: Form Wizard Step 2-->

                  <!--begin: Form Wizard Step 3-->
                  <div class="kt-wizard-v3__content" data-ktwizard-type="step-content">
                    <div class="kt-container">
                      <div class="row">
                      </div>
                    </div>
                  </div>
                  <!--end: Form Wizard Step 3-->

                  <!--begin: Form Actions -->
                  <div class="kt-form__actions kt-container">
                    <button class="btn btn-secondary btn-md btn-tall btn-wide kt-font-bold kt-font-transform-u" data-ktwizard-type="action-prev">
                      Önceki
                    </button>
                    <button class="btn btn-success btn-md btn-tall btn-wide kt-font-bold kt-font-transform-u" data-ktwizard-type="action-submit">
                      Kaydet
                    </button>
                    <button class="btn btn-brand btn-md btn-tall btn-wide kt-font-bold kt-font-transform-u" data-ktwizard-type="action-next">
                      Sonraki
                    </button>
                  </div>
                  <!--end: Form Actions -->
                </form>

                <!--end: Form Wizard Form-->
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- end:: Content -->
    </div>
</template>

<script>
  import VueDragResize from 'vue-drag-resize';
  import draggable from "vuedraggable";
  import designInvoiceForm from "../../../../assets/js/custom/pages/designInvoiceForm";

  export default {
      name: "DesignInvoiceForm",
      components: {
        VueDragResize,
        draggable
      },
      data(){
        return {
          list1: [],
          list2: [
            {id: 0, type: "object", name: "products", title: "Ürün Bilgileri"},
            {id: 1, type: "text", name: "fixedText", title: "Sabit Metin"},
            {id: 2, type: "text", name: "marketPlaceName", title: "Pazaryeri Adı"},
            {id: 3, type: "text", name: "marketPlaceOrderNo", title: "Pazaryeri Sipariş No"},
            {id: 4, type: "text", name: "cargoTag", title: "Kargo Etiketi"},
          ],
          list3: [
            {id: 0, type: "text", name: "customerName", title: "Ad Soyad"},
            {id: 1, type: "text", name: "address", title: "Adres"},
            {id: 2, type: "text", name: "town", title: "İlçe"},
            {id: 3, type: "text", name: "city", title: "Şehir"},
            {id: 4, type: "text", name: "postCode", title: "Posta Kodu"},
            {id: 5, type: "text", name: "phone", title: "Telefon"},
            {id: 6, type: "text", name: "taxDepartment", title: "Vergi Dairesi"},
            {id: 7, type: "text", name: "taxNo", title: "TC/Vergi Numarası"},
          ],
          list4: [
            {id: 0, type: "text", name: "invoiceDate", title: "Fatura Tarihi"},
            {id: 1, type: "text", name: "invoiceTime", title: "Fatura Saati"},
            {id: 2, type: "text", name: "subTotal", title: "Ara Toplam"},
            {id: 3, type: "text", name: "taxTotal", title: "Vergi Toplam"},
            {id: 4, type: "text", name: "taxTable", title: "Vergi Tablosu"},
            {id: 5, type: "text", name: "invoiceTotal", title: "Fatura Toplam"},
            {id: 6, type: "text", name: "invoiceTotalText", title: "Fatura Toplam Yazılı"},
          ],
          pageType: 1,
          pageWidth: "209mm",
          pageHeight: "297mm",
          pageScale: "scale(0.920)",
          invoiceDesignId: this.$route.params.id,
        }
      },
      computed: {
        rects() {
          return this.$store.state.rect.rects
        }
      },
      created: function () {
        setTimeout(function(){
          console.debug("DesignInvoiceForm Time: 3000");
          designInvoiceForm.init();
        }, 1000);
      },
      methods: {
        pageTypeChangeEvent(){
          if(this.pageType === "1"){
            this.pageWidth = "209mm";
            this.pageHeight = "297mm";
            this.pageScale = "scale(0.920)";
          } else if (this.pageType === "2"){
            this.pageWidth = "297mm";
            this.pageHeight = "209mm";
            this.pageScale = "scale(0.649)";
          } else {
            this.pageWidth = "209mm";
            this.pageHeight = "297mm";
            this.pageScale = "scale(0.920)";
          }
        },
        activateEv(index) {
          this.$store.dispatch('rect/setActive', {id: index});
        },
        deactivateEv(index) {
          this.$store.dispatch('rect/unsetActive', {id: index});
        },
        removeEv(index){
          this.$store.dispatch('rect/removeElement', {id: index});
        },
        changePosition(newRect, index) {
          this.$store.dispatch('rect/setTop', {id: index, top: newRect.top});
          this.$store.dispatch('rect/setLeft', {id: index, left: newRect.left});
          this.$store.dispatch('rect/setWidth', {id: index, width: newRect.width});
          this.$store.dispatch('rect/setHeight', {id: index, height: newRect.height});
        },
        changeSize(newRect, index) {
          this.$store.dispatch('rect/setTop', {id: index, top: newRect.top});
          this.$store.dispatch('rect/setLeft', {id: index, left: newRect.left});
          this.$store.dispatch('rect/setWidth', {id: index, width: newRect.width});
          this.$store.dispatch('rect/setHeight', {id: index, height: newRect.height});
        },
        addElement(param){
          this.$store.dispatch('rect/addElement', {param: param});
        },
        add: function() {
          this.list.push({ name: "Juan" });
        },
        replace: function() {
          this.list = [{ name: "Edgard" }];
        },
        clone: function(el) {
          return {
            name: el.name + " cloned"
          };
        },
        log: function(evt) {
          window.console.log(evt);
        },
        onAdd: function(evt) {
          this.addElement(evt.item._underlying_vm_);
          window.console.log(evt);
        },
        onRemove: function(evt) {
          window.console.log(evt);
        }
      }
    }
</script>

<style scoped>
  .filler {
    width: 100%;
    padding: 3px;
    font-size: 10pt;
    color: black;
    font-weight: 500;
    height: 100%;
    display: inline-table;
    position: absolute;
    background-color: transparent!important;
    border: 2px dashed #5477e8;
  }
  .invoice-drop-area .list-group-item {
    height: 100%;
    color: transparent;
    background-color: transparent;
  }
  .invoicePage .vdr.active .btn{
    display: block;
  }
  .invoicePage .vdr.inactive .btn{
    display: none;
  }
  .invoicePage .vdr-btn-delete{
    right: 0px;
    bottom: -38px;
    position: absolute;
  }
  .kt-wizard-v3 .kt-wizard-v3__nav .kt-wizard-v3__nav-items {
    justify-content: center;
  }
</style>



