const state = {
  product: null,
  products: []
};

const getters = {
  getProducts(state, rootState){
    return state.products;
  },
  getProduct(state, rootState){
    return key => state.products.filter(element => {
      if(element.id == key) {
        state.product = element;
        return state.product;
      }
    });
  }
};

// Senkron
const mutations = {
};

// Asenkron
const actions = {
};

const localProduct = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};

export default localProduct



