import axios from "axios";
import querystring from "querystring";
import {router} from "../../router";
import store from "../store";

const user = {
  namespaced: true,
  state: {
    oneSignal: null,
    data: {
      id: null,
      fname: "",
      lname: null,
      email: null,
      gsm: null,
      tckn: null,
      create_date: null
    },
    address: {
      id: null,
      addr_name: null,
      country: null,
      city: null,
      street1: null,
      street2: null,
      zip_code: null,
    },
    token: "",
  },
  getters: {
    isAuthenticated(state) {
      //return state.token !== "";
      return true;
    },
    getInfo(state){
      return state.data;
    },
    getAddress(state){
      return state.address;
    },
    dataValidation(state){
      console.debug("dataValidation");
      for (const keyData in state.data) {
        if(state.data[keyData] === null || state.data[keyData] === "") return false;
      }
      for (const keyAddress in state.address) {
        if((state.address[keyAddress] === null || state.address[keyAddress] === "") && (keyAddress != "street2")) return false;
      }
      return true;
    }
  },
  mutations: {
    setToken(state, token) {
      if(token !== undefined && token !== null && token !== "" && token !== "undefined") {
        state.token = token;
        localStorage.setItem("token", token);
        localStorage.setItem("expirationDate", new Date().getTime() + config_expiresIn);
      }
    },
    clearToken(state) {
      state.token = "";
      state.data = null;
      state.address = null;
      localStorage.removeItem("token");
      localStorage.removeItem("expirationDate");
      router.replace("/Login");
    },
    setStateInfo(state, userData) {
      if(userData !== undefined) state.data = userData;
    },
    setStateAddress(state, addressData) {
      if(addressData !== undefined) state.address = addressData;
    },
  },
  actions: {
    initAuth({commit, dispatch}){
      let token = localStorage.getItem('token');
      if(token){
        let expirationDate = localStorage.getItem('expirationDate');
        let time = new Date().getTime();
        if(time > +expirationDate){
          dispatch("logout");
        }else{
          commit("setToken", token);
          dispatch("setTimeoutTimer", (+expirationDate - time));
        }
      }else{
        return false;
      }
    },
    login({commit, dispatch, state, rootState}, authData) {
      var form = $(authData.element.srcElement);
      var btn = form.find(':submit');
      var loginConfigHeader = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      };
      if (authData.formType == "login") {
        form.validate({
          rules: {
            email: {
              required: true,
              email: true
            },
            password: {
              required: true
            },
          },
          messages: {
            email: {
              required: lang('VALIDATION.REQUIRED'),
              email: lang('VALIDATION.MAIL_FORMAT')
            },
            password: {
              required: lang('VALIDATION.REQUIRED'),
            },
          }
        });
        if (!form.valid()) {
          return;
        }
        btn.addClass('kt-spinner kt-spinner--right kt-spinner--sm kt-spinner--light').attr('disabled', true);
        axios.post('auth/users/login', querystring.stringify({email: authData.email,password: authData.password}), loginConfigHeader).then(response => {
            if (response.data.input.result === true || response.data.input.result === "true") {
              if(response.data.input.Token !== undefined) {
                KTLoginGeneral.showErrorMsg(form, 'success', lang('AUTH.LOGIN_SUCCESS'));
                setTimeout(function (e) {
                  commit("setToken", response.data.input.Token);
                  dispatch("setTimeoutTimer", config_expiresIn);
                  dispatch("getInfo");
                  dispatch("initOneSignal");
                  router.push("/");
                }, 1000);
              } else {
                KTLoginGeneral.showErrorMsg(form, 'danger', response.data.message);
              }
            } else {
              KTLoginGeneral.showErrorMsg(form, 'danger', lang('AUTH.LOGIN_ERROR'));
            }
          }
        ).catch(function (error) {
          KTLoginGeneral.showErrorMsg(form, 'danger', lang('GLOBAL.PLEASE_TRY_AGAIN'));
        }).then(function () {
          btn.removeClass('kt-spinner kt-spinner--right kt-spinner--sm kt-spinner--light').attr('disabled', false); // remove
        });
      } else if (authData.formType == "register") {
        form.validate({
          rules: {
            fname: {
              required: true
            },
            lname: {
              required: true
            },
            email: {
              required: true,
              email: true
            },
            password: {
              required: true
            },
            rpassword: {
              required: true
            },
            agree: {
              required: true
            }
          },
          messages: {
            fname: {
              required: lang('VALIDATION.REQUIRED'),
            },
            lname: {
              required: lang('VALIDATION.REQUIRED'),
            },
            email: {
              required: lang('VALIDATION.REQUIRED'),
              email: lang('VALIDATION.MAIL_FORMAT')
            },
            password: {
              required: lang('VALIDATION.REQUIRED'),
            },
            rpassword: {
              required: lang('VALIDATION.REQUIRED'),
            },
          }
        });
        if (!form.valid()) {
          return;
        }
        if(!authData.agree){
          KTLoginGeneral.showErrorMsg(form, 'warning', lang('AUTH.VALIDATE_AGREE'));
          return;
        }
        btn.addClass('kt-spinner kt-spinner--right kt-spinner--sm kt-spinner--light').attr('disabled', true);
        axios.post('auth/users/create', querystring.stringify({
          fname: authData.fname,
          lname: authData.lname,
          email: authData.email,
          password: authData.password,
          rpassword: authData.rpassword,
          agree: authData.agree,
        }), loginConfigHeader).then(response => {
            if (response.data.input.result === true || response.data.input.result === "true") {
              if(response.data.input.Token !== undefined) {
                KTLoginGeneral.showErrorMsg(form, 'success', lang('AUTH.REGISTER_SUCCESS'));
                setTimeout(function (e) {
                  commit("setToken", response.data.input.Token);
                  dispatch("setTimeoutTimer", config_expiresIn);
                  dispatch("getInfo");
                  router.push("/Api");
                }, 1000);
              }else {
                KTLoginGeneral.showErrorMsg(form, 'danger', response.data.message);
              }
            } else {
              KTLoginGeneral.showErrorMsg(form, 'danger', response.data.message);
            }
          }
        ).catch(function (error) {
          KTLoginGeneral.showErrorMsg(form, 'danger', lang('GLOBAL.PLEASE_TRY_AGAIN'));
        }).then(function () {
          btn.removeClass('kt-spinner kt-spinner--right kt-spinner--sm kt-spinner--light').attr('disabled', false); // remove
        });
      } else {
        axios.post('auth/users/password', querystring.stringify({email: authData.email})).then(response => {
          }
        );
      }
    },
    logout({commit, dispatch, state}) {
      commit("clearToken");
    },
    setTimeoutTimer({dispatch}, expiresIn){
      setTimeout(() => {
        dispatch("logout");
      }, expiresIn);
    },
    getInfo({commit, dispatch, getters}) {
      if(getters.isAuthenticated){
        axios.get('user/info').then(response => {
            if (response.data.success) {
              if(response.data.result !== undefined) {
                commit("setStateInfo", response.data.result[0]);
                dispatch("init");
              }
            }
          }
        ).catch(function (error) {

        });
      }
    },
    setInfo({commit, dispatch, state, rootState}, userData){
      var form = $(userData.element.srcElement);
      var btn = form.find(':submit');

      form.validate({
        rules: {
          fname: {
            required: true
          },
          lname: {
            required: true
          },
          email: {
            required: true,
            email: true
          },
          gsm: {
            required: true,
          },
          tckn: {
            required: true,
            minlength: 11,
            maxlength: 11
          }
        }
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(userData.element.srcElement, lang('GLOBAL.SAVING'));

      var userDataParam = {};
      Object.assign(userDataParam, userData);
      delete userDataParam.id;
      delete userDataParam.create_date;
      delete userDataParam.element;

      axios.put('user/info', userDataParam).then(response => {
        if (response.data.success) {
          Fmessage(null, lang('GLOBAL.RECORD_UPDATED'), "success");
          dispatch("getInfo");
        } else {
          Fmessage(null, response.data.message, "error");
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(btn[0]);
        FUnBlockLoading(userData.element.srcElement);
      });

    },
    getAddress({commit, getters}) {
      if(getters.isAuthenticated){
        axios.get('user/address').then(response => {
          if (response.data.success) {
            if(response.data.result !== undefined) {
              commit("setStateAddress", response.data.result[0]);
            }
          }
        }).catch(function (error) {

        });
      }
    },
    setAddress({commit, dispatch, state, rootState}, addressData){

      var form = $(addressData.element.srcElement);
      var btn = form.find(':submit');

      form.validate({
        rules: {
          addr_name: {
            required: true
          },
          country: {
            required: true
          },
          city: {
            required: true,
          },
          street1: {
            required: true,
          },
          zip_code: {
            required: true,
          }
        }
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(addressData.element.srcElement, lang('GLOBAL.SAVING'));

      var dataParam = {};
      Object.assign(dataParam, addressData);
      delete dataParam.id;
      delete dataParam.element;

      if(addressData.id !== null) {
        axios.put('user/address/' + addressData.id, dataParam).then(response => {
          if(response.data.success) {
            Fmessage(null, lang('GLOBAL.RECORD_UPDATED'), "success");
            dispatch("getAddress");
          }else{
            Fmessage(null, response.data.message, "error");
          }
        }).catch(function (error) {
          axiosCatchMessage(error);
        }).then(function () {
          FUnloading(btn[0]);
          FUnBlockLoading(addressData.element.srcElement);
        });
      }else{
        dataParam.street2 = "";
        axios.post('user/address', dataParam).then(response => {
          if(response.data.success) {
            Fmessage(null, lang('GLOBAL.RECORD_ADDED'), "success");
            dispatch("getAddress");
          }else{
            Fmessage(null, response.data.message, "error");
          }
        }).catch(function (error) {
          axiosCatchMessage(error);
        }).then(function () {
          FUnloading(btn[0]);
          FUnBlockLoading(addressData.element.srcElement);
        });
      }

    },
    init({commit, dispatch, state, rootState}){
      dispatch("getAddress");
      store.dispatch("api/getPlatforms");
      store.dispatch("api/getApiList");
      store.dispatch("payment/getSubscription");
      store.dispatch("payment/getLicense");
    },
    initOneSignal({commit, getters, dispatch, state, rootState}){
      state.oneSignal = window.OneSignal || [];
      var OneSignal = state.oneSignal;
      OneSignal.push(function() {
        OneSignal.init({
          appId: "51965faf-42f8-44bc-8fa0-c61b5f1df1dd",
        });
        // Abonelik durumu değiştiğinde tetiklenir.
        OneSignal.on('subscriptionChange', function (isSubscribed) {
          // True or False
          console.log("The user's subscription state is now:", isSubscribed);
          // Eğer abonelik aktif ise kullanıcı ID bilgisini gönder
          if(isSubscribed) {
            // Kullanıcı ID bilgisini verir.
            OneSignal.getUserId().then(function (val) {
              console.log(val);
              if(getters.isAuthenticated){
                axios.post('user/notification/device/add?id=' + val).then(response => {
                    if (response.data.success) {
                      if(response.data.result !== undefined) {

                      }
                    }
                  }
                ).catch(function (error) {

                });
              }
            });
          }
        });
        // Tarayıcı izni değiştiğinde tetiklenir.
        OneSignal.on('notificationPermissionChange', function(permissionChange) {
          var currentPermission = permissionChange.to;
          // denied or granted
          console.log('New permission state:', currentPermission);
        });
        // Abonelik başlatma ve iptal etme true or false
        //OneSignal.push(["setSubscription", false]);
        // Mevcut abonelik durumu true or false
        OneSignal.isPushNotificationsEnabled(function(isEnabled) {
          if (isEnabled)
            console.log("Push notifications are enabled!");
          else
            console.log("Push notifications are not enabled yet.");
        });

      });
    },
    setInfoAndAddress({commit, dispatch, state, rootState}, paramsData){
      dispatch("setInfo", {...paramsData.userInfo, element: paramsData.element});
      dispatch("setAddress", {...paramsData.addressInfo, element: paramsData.element});
    },
  },
};

export default user



