<template>
  <div>
    <!-- begin:: Subheader -->
    <div class="kt-subheader   kt-grid__item" id="kt_subheader">
      <div class="kt-container  kt-container--fluid ">
        <div class="kt-subheader__main">
          <h3 class="kt-subheader__title">API DETAY</h3>
          <span class="kt-subheader__separator kt-hidden"></span>
          <div class="kt-subheader__breadcrumbs">
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-home" active-class="kt-subheader__breadcrumbs-link--active" exact><i class="flaticon2-shelter"></i></router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ana Sayfa</router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/Api" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Api Listesi</router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" :to="'/Api/Detail/' + $route.params.id" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Api Detay</router-link>
          </div>
        </div>
      </div>
    </div>
    <!-- end:: Subheader -->

    <!-- begin:: Content -->
    <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">

      <!--begin::Pricing Tables Page - 3-->

      <!--begin::Portlet-->
      <div class="kt-portlet">
        <div class="kt-portlet__head">
          <div class="kt-portlet__head-label">
            <span class="kt-portlet__head-icon">
              <i class="la la-puzzle-piece"></i>
            </span>
            <h3 class="kt-portlet__head-title">
              {{ getPlatform != null ? getPlatform.name : "" }}
            </h3>
          </div>
        </div>
        <div class="kt-portlet__body">
          <form v-if="formData!==null" class="kt-form kt-form--label-right" id="kt_form_3" @submit.prevent="onsubmit">
            <div class="kt-section">
              <div class="kt-section__content">

                <div class="form-group row" v-for="field in getPlatformFields">
                  <div class="col-sm-12 col-lg-12" v-if="field.column !== 'active'">
                    <label class="form-control-label">* {{field.name}}:</label>
                    <input type="text" v-model="formData[field.column]" :name="field.column" class="form-control" placeholder="">
                  </div>
                  <div class="col-sm-12 col-lg-12" v-else>
                    <label class="form-control-label"></label>
                    <label class="kt-checkbox kt-checkbox--bold kt-checkbox--success">
                      <input type="checkbox" v-model="formData[field.column]" :name="field.column"> {{field.name}}
                      <span></span>
                    </label>
                  </div>
                </div>

              </div>
            </div>
            <div class="col-md-12">
              <button type="submit" class="btn btn-success float-right mt-2">Kaydet</button>&nbsp;
              <router-link tag="a" to="/Api" class="btn btn-secondary float-right mr-2 mt-2">İptal</router-link>
            </div>

          </form>

        </div>
      </div>

      <!--end::Portlet-->

      <!--begin::Pricing Tables Page - 3-->
    </div>
    <!-- end:: Content -->
  </div>
</template>

<script>
  import {router} from "../../router";
  import apiPage from "../../assets/js/custom/pages/api";
  var tmpApiForm = null;
  export default {
    name: "ApiForm",
    data(){
      return {
        apiId: this.$route.params.id,
        api: null,
        platformId: null,
        formData: {},
        getApiWatch: null,
      }
    },
    computed: {
      getPlatformFields(){
        var param = this.$store.getters['api/getPlatformFields'];
        if(param.length>0 && this.api !== null){
          var tmpObjectForm = {};
          param.push({name:'Durum', column:'active'});
          for(var i=0; i<param.length; i++){
            tmpObjectForm[param[i].column] = this.api[param[i].column];
          }
          this.formData = tmpObjectForm;
          return param;
        }
        return null;
      },
      getPlatform(){
        var param = this.$store.getters['api/getPlatform'](this.platformId);
        return param.length>0 ? param[0] : null;
      },
      getApi(){
        var param = this.$store.getters['api/getApi'];
        if(param!=null){
          if(this.getApiWatch !== null) this.getApiWatch();
          this.platformId = param.platformid;
          this.api = param;
          return param;
        }
        return null;
      },
    },
    created: function () {
      console.debug("ApiForm Created");
      tmpApiForm = this;
      this.$store.dispatch("api/getApi", {id: this.$route.params.id});

      this.getApiWatch = this.$store.watch((state, getters) => this.$store.getters['api/getApi'],(newValue, oldValue) => {
          tmpApiForm.getApi;
          this.$store.dispatch("api/getPlatformFields", {platformId: newValue.platformid});
        },
      );

      this.$store.watch((state, getters) => this.$store.getters['api/getPlatformFields'],(newValue, oldValue) => {
        //tmpApiForm.getPlatformFields;
        //tmpApiForm.getPlatform;
        },
      );

    },
    methods: {
      onsubmit(element){
        this.$store.dispatch("api/updateApi", {...this.formData, id: this.apiId, pid: this.platformId, element: element})
      }
    }
  }
</script>

<style scoped>

</style>



