import axios from "axios";
import {router} from "../../router";

const product = {
  namespaced: true,
  state:{
    products: [],
    liveProduct: null,
  },
  getters:{
    getProducts(state) {
      console.debug("product/getters/getProducts");
      return state.products !== undefined ? state.products : false;
    },
    getLiveProduct(state) {
      console.debug("product/getters/getLiveProduct");
      return state.liveProduct !== undefined && state.liveProduct !== null ? state.liveProduct : false;
    },
  },
  mutations:{
    setProducts(state, data) {
      console.debug("product/setProducts");
      if(data !== undefined) state.products = data;
    },
    setLiveProduct(state, data) {
      console.debug("product/setLiveProduct");
      if(data !== undefined) state.liveProduct = data;
    },
  },
  actions:{
    getProducts({commit, getters, rootState}) {
      console.debug("product/getProducts");
      FBlockLoading($('#kt_content'), null);
      axios.get('product/getproducts').then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setProducts", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    getLiveProduct({commit, getters, rootState}, params) {
      console.debug("product/getLiveProduct");
      axios.get('product/getliveproduct/' + params.subProductId).then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setLiveProduct", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
      });
    },
    updateProduct({commit, dispatch, state, rootState}, params){
      console.debug("product/updateProduct");

      var form = $(params.element.srcElement);
      var btn = form.find(':submit');

      Floading(btn[0]);
      FBlockLoading(form, lang('GLOBAL.SAVING'));

      //params.detailData.Price = parseFloat(params.detailData.Price);

      var dataParam = {};
      Object.assign(dataParam, params);
      delete dataParam.element;

      axios.put('product/updateproduct/'+params.subid, dataParam).then(response => {
        if(response.data.success) {
          Fmessage(null, lang('GLOBAL.RECORD_UPDATED'), "success");
        }else{
          Fmessage(null, response.data.message, "error");
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(btn[0]);
        FUnBlockLoading(form);
      });

    },
  },
};

export default product



