<template>
    <div>
      <!-- begin:: Page -->
      <div class="kt-grid kt-grid--ver kt-grid--root">
        <div class="kt-grid kt-grid--hor kt-grid--root  kt-login kt-login--v6 kt-login--signin" id="kt_login">
          <div class="kt-grid__item kt-grid__item--fluid kt-grid kt-grid--desktop kt-grid--ver-desktop kt-grid--hor-tablet-and-mobile">
            <div class="kt-grid__item  kt-grid__item--order-tablet-and-mobile-2  kt-grid kt-grid--hor kt-login__aside">
              <div class="kt-login__wrapper">
                <div class="kt-login__container">
                  <div class="kt-login__body">
                    <div class="kt-login__logo">
                      <a href="#">
                        <img src="/src/assets/images/settings/iltechlogo.png">
                      </a>
                    </div>
                    <div class="kt-login__signin">
                      <div class="kt-login__head">
                        <h3 class="kt-login__title">Giriş Yap</h3>
                      </div>
                      <div class="kt-login__form">
                        <form class="kt-form" @submit.prevent="onsubmit" data-form-type="login">
                          <div class="form-group">
                            <input class="form-control m_inputmask_email" type="email" placeholder="E-Posta" name="email" v-model="user.email" autocomplete="off">
                          </div>
                          <div class="form-group">
                            <input class="form-control form-control-last" type="password" placeholder="Parola" name="password" v-model="user.password">
                          </div>
                          <div class="kt-login__extra">
                            <label class="kt-checkbox">
                              <input type="checkbox" v-model="user.remember"> Beni Hatırla
                              <span></span>
                            </label>
                            <a href="javascript:;" id="kt_login_forgot"> Parolamı unuttum</a>
                          </div>
                          <div class="kt-login__actions">
                            <button type="submit" id="kt_login_signin_submit_iptal" class="btn btn-brand btn-pill btn-elevate">Giriş Yap</button>
                          </div>
                        </form>
                      </div>
                    </div>
                    <div class="kt-login__signup">
                      <div class="kt-login__head">
                        <h3 class="kt-login__title">Kayıt Ol</h3>
                        <div class="kt-login__desc">Hemen hızlı kaydınızı oluşturun:</div>
                      </div>
                      <div class="kt-login__form">
                        <form class="kt-form" @submit.prevent="onsubmit" data-form-type="register">
                          <div class="form-group">
                            <input class="form-control" type="text" placeholder="Ad" name="fname" v-model="user.fname">
                          </div>
                          <div class="form-group">
                            <input class="form-control" type="text" placeholder="Soyad" name="lname" v-model="user.lname">
                          </div>
                          <div class="form-group">
                            <input class="form-control m_inputmask_email" type="email" placeholder="E-Posta" name="email" v-model="user.email" autocomplete="off">
                          </div>
                          <div class="form-group">
                            <input class="form-control" type="password" placeholder="Parola" name="password" v-model="user.password">
                          </div>
                          <div class="form-group">
                            <input class="form-control form-control-last" type="password" name="rpassword" placeholder="Parola Tekrar" v-model="user.rpassword">
                          </div>
                          <div class="kt-login__extra">
                            <label class="kt-checkbox">
                              <input type="checkbox" v-model="user.agree"> Kabul ediyorum <a href="https://www.iltechsoft.com/tr/kullanici-sozlesmesi/" target="_blank">kullanıcı sözleşmesi</a>.
                              <span></span>
                            </label>
                          </div>
                          <div class="kt-login__actions">
                            <button type="submit" id="kt_login_signup_submit_iptal" class="btn btn-brand btn-pill btn-elevate">Kayıt Ol</button>
                            <button id="kt_login_signup_cancel" class="btn btn-outline-brand btn-pill">İptal</button>
                          </div>
                        </form>
                      </div>
                    </div>
                    <div class="kt-login__forgot">
                      <div class="kt-login__head">
                        <h3 class="kt-login__title">Parolamı Unuttum</h3>
                        <div class="kt-login__desc">Parolanızı sıfırlamak için E-Posta adresinizi girin:</div>
                      </div>
                      <div class="kt-login__form">
                        <form class="kt-form" @submit.prevent="onsubmit" data-form-type="password">
                          <div class="form-group">
                            <input class="form-control" type="text" placeholder="E-Posta" v-model="user.email" id="kt_email" autocomplete="off">
                          </div>
                          <div class="kt-login__actions">
                            <button type="submit" id="kt_login_forgot_submit_iptal" class="btn btn-brand btn-pill btn-elevate">Gönder</button>
                            <button id="kt_login_forgot_cancel" class="btn btn-outline-brand btn-pill">İptal</button>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="kt-login__account">
								<span class="kt-login__account-msg">
									Henüz hesabınız yok mu ?
								</span>&nbsp;&nbsp;
                  <a href="javascript:;" id="kt_login_signup" class="kt-login__account-link">Kayıt Ol!</a>
                </div>
              </div>
            </div>
            <!--<div class="kt-grid__item kt-grid__item--fluid kt-grid__item--center kt-grid kt-grid--ver kt-login__content" style="background-image: url(/src/assets/media/bg/bg-4.jpg);">-->
            <div class="kt-grid__item kt-grid__item--fluid kt-grid__item--center kt-grid kt-grid--ver kt-login__content" style="background-color: #10619C;">
              <div class="kt-login__section">
                <div class="kt-login__block">
                  <h3 class="kt-login__title">Entegrasyon Yazılımı</h3>
                  <div class="kt-login__desc">
                    Yeni nesil bulut tabanlı e-ticaret entegrasyon yazılımı.
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- end:: Page -->
    </div>
</template>

<script>
    export default {
      name: "Auth",
      data(){
        return {
          formType: "login",
          user: {
            fname: null,
            lname: null,
            email: null,
            password: null,
            rpassword: null,
            remember: false,
            agree: false
          }
        }
      },
      created: function () {
        var path = this.$route.path;
        setTimeout(function () {
          console.debug("KT LOGIN GENERAL");
          KTLoginGeneral.init();
          if(path === "/Register"){
            KTLoginGeneral.displaySignUpForm();
          }
        }, 100);
      },
      methods: {
        onsubmit(element){
          this.formType = element.srcElement.dataset.formType;
          this.$store.dispatch("user/login", {...this.user, formType: this.formType, element: element})
        }
      }
    }
</script>

<style scoped>

</style>



