<template>
  <div>

    <div class="modal fade" id="paymentListDetailModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Ödeme Detayı</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            </button>
          </div>
          <div class="modal-body">
            <div class="row" v-if="getPaymentDetailModalData!=null">
              <div class="col-xs-12 col-md-4">
                <b>Kart Kuruluş</b>
                <p>{{getPaymentDetailModalData.cardAssociation}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Kart Aile</b>
                <p>{{getPaymentDetailModalData.cardFamily}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Kart Tipi</b>
                <p>{{getPaymentDetailModalData.cardType}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Ödeme Türü</b>
                <p>{{getPaymentDetailModalData.paymentType}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Tutar</b>
                <p>{{getPaymentDetailModalData.totalPrice}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Toplam Tutar</b>
                <p>{{getPaymentDetailModalData.totalPaidPrice}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Bin Numarası</b>
                <p>{{getPaymentDetailModalData.binNumber}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Taksit</b>
                <p>{{getPaymentDetailModalData.installment}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Ödeme ID</b>
                <p>{{getPaymentDetailModalData.paymentId}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Hata Kodu:</b>
                <p>{{getPaymentDetailModalData.errorCode}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Hata Mesajı:</b>
                <p>{{getPaymentDetailModalData.errorMessage}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Durum</b>
                <p>{{getPaymentDetailModalData.status == "success" ? "Başarılı" : "Başarısız"}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Tarih</b>
                <p>{{getPaymentDetailModalData.createDate}}</p>
              </div>
              <div class="col-xs-12 col-md-4">
                <b>Ödeme Tipi</b>
                <p>{{getPaymentDetailModalData.paymentType}}</p>
              </div>
            </div>
            <div class="row" v-else>
              <p>Ödeme detayı bulunamadı!</p>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Kapat</button>
          </div>
        </div>
      </div>
    </div>

    <!-- begin:: Subheader -->
    <div class="kt-subheader   kt-grid__item" id="kt_subheader">
      <div class="kt-container  kt-container--fluid ">
        <div class="kt-subheader__main">
          <h3 class="kt-subheader__title">Ödeme Geçişi</h3>
          <span class="kt-subheader__separator kt-hidden"></span>
          <div class="kt-subheader__breadcrumbs">
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-home" active-class="kt-subheader__breadcrumbs-link--active" exact><i class="flaticon2-shelter"></i></router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ana Sayfa</router-link>
            <span class="kt-subheader__breadcrumbs-separator"></span>
            <router-link tag="a" to="/Buy/PaymentList" class="kt-subheader__breadcrumbs-link" active-class="kt-subheader__breadcrumbs-link--active" exact>Ödeme Geçmişi</router-link>
          </div>
        </div>
        <div class="kt-subheader__toolbar">
          <div class="kt-subheader__wrapper">
            <router-link tag="a" to="/Buy" class="btn kt-subheader__btn-primary">
              Ödeme Yap
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <!-- end:: Subheader -->

    <!-- begin:: Content -->
    <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
      <div class="kt-portlet kt-portlet--mobile">
        <div class="kt-portlet__head kt-portlet__head--lg">
          <div class="kt-portlet__head-label">
            <span class="kt-portlet__head-icon">
              <i class="kt-font-brand flaticon2-line-chart"></i>
            </span>
            <h3 class="kt-portlet__head-title">
              Ödeme Geçmişi
            </h3>
          </div>
        </div>
        <div class="kt-portlet__body">
          <!--begin: Search Form -->
          <div class="kt-form kt-form--label-right ">
            <div class="row align-items-center">
              <div class="col-xl-8 order-2 order-xl-1">
                <div class="row align-items-center">
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                    <div class="kt-input-icon kt-input-icon--left">
                      <input type="text" class="form-control" placeholder="Ara..." id="generalSearch">
                      <span class="kt-input-icon__icon kt-input-icon__icon--left">
                        <span><i class="la la-search"></i></span>
                      </span>
                    </div>
                  </div>
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                    <div class="kt-form__group kt-form__group--inline">
                      <div class="kt-form__label">
                        <label>Status:</label>
                      </div>
                      <div class="kt-form__control">
                        <select class="form-control bootstrap-select" id="kt_form_status">
                          <option value="">Hepsi</option>
                          <option value="4">Aktif</option>
                          <option value="6">Pasif</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-md-4 kt-margin-b-20-tablet-and-mobile">
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!--end: Search Form -->
        </div>
        <div class="kt-portlet__body kt-portlet__body--fit">

          <!--begin: Datatable -->
          <div class="kt-datatable" id="local_data"></div>

          <!--end: Datatable -->
        </div>
      </div>
    </div>
    <!-- end:: Content -->
  </div>
</template>

<script>
    import paymentListPage from "../../assets/js/custom/pages/paymentList";
    export default {
      name: "PaymentList",
      data(){
        return {}
      },
      computed: {
        getPaymentDetailModalData(){
          return this.$store.getters['payment/getPaymentDetailModalData'];
        },
      },
      created: function () {
        paymentListPage.init(this);
        this.$store.dispatch('payment/getPaymentList');
        this.unwatch = this.$store.watch((state, getters) => this.$store.getters['payment/getPaymentList'],(newValue, oldValue) => {
            paymentListPage.setListData(newValue);
          },
        );
      },
      beforeDestroy() {
        this.unwatch();
      }
    }
</script>

<style scoped>

</style>



