import axios from "axios";
import {router} from "../../router";

const order = {
  namespaced: true,
  state:{
    data: null,
    platforms: [],
    apiList: [],
    platformFields: [],
    products: [],
  },
  getters:{
    getApiList(state) {
      console.debug("api/getters/getApiList");
      return state.apiList !== undefined ? state.apiList : false;
    },
    getApi(state){
      console.debug("api/getters/getApi");
      return state.data;
    },
    getPlatforms(state) {
      console.debug("api/getters/getPlatforms");
      return state.platforms !== undefined ? state.platforms : false;
    },
    getPlatform(state, rootState){
      console.debug("api/getters/getPlatform");
      return key => state.platforms.filter(element => {
        if(element.id == key) {
          return element;
        }
      });
    },
    getPlatformFields(state) {
      console.debug("api/getters/getPlatformFields");
      return state.platformFields !== undefined ? state.platformFields : false;
    },
    getProducts(state){
      console.debug("api/getters/getProducts");
      return state.products !== undefined ? state.products : false;
    }
  },
  mutations:{
    setApiList(state, data) {
      console.debug("api/setApiList");
      if(data !== undefined) state.apiList = data;
    },
    setApi(state, data) {
      console.debug("api/setApi");
      if(data !== undefined) state.data = data[0];
    },
    setPlatforms(state, data) {
      console.debug("api/setPlatforms");
      if(data !== undefined) state.platforms = data;
    },
    setPlatformFields(state, data) {
      console.debug("api/setPlatformFields");
      if(data !== undefined) state.platformFields = data;
    },
    setProducts(state, data){
      console.debug("api/setProducts");
      if(data !== undefined) state.products = data;
    }
  },
  actions:{
    getApiList({commit, getters, rootState}) {
      console.debug("api/getApiList");
      FBlockLoading($('#kt_content'), null);
      axios.get('api/list').then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setApiList", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    getApi({commit, getters, rootState}, params) {
      console.debug("api/getApi");
      FUnBlockLoading($('#kt_content'), null);
      axios.get('api/'+params.id).then(response => {
        if (response.data.success) {
          if (response.data.result !== undefined) {
            commit("setApi", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    setApi({commit, dispatch, state, rootState}, params){
      console.debug("api/setApi");

      var form = $(params.element.srcElement);
      var btn = form.find(':submit');

      var tmpParams = {};
      Object.assign(tmpParams, params);
      for (const property in tmpParams) {
        tmpParams[property] = {required: true};
      }
      form.validate({
        rules: tmpParams
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(form, lang('GLOBAL.SAVING'));

      var dataParam = {};
      Object.assign(dataParam, params);
      delete dataParam.element;

      axios.post('api/add/', dataParam).then(response => {
        if(response.data.success) {
          Fmessage(null, lang('GLOBAL.RECORD_ADDED'), "success");
          setTimeout(function (e) {
            //router.push("/Api");
            dispatch("setApiProductToLocalProduct", {apiId: response.data.result.recordID, totalProduct: response.data.result.TotalProductsToImport});
          }, 1000);
        }else{
          Fmessage(null, response.data.message, "error");
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(btn[0]);
        FUnBlockLoading(form);
      });

    },
    updateApi({commit, dispatch, state, rootState}, params){
      console.debug("api/updateApi");

      var form = $(params.element.srcElement);
      var btn = form.find(':submit');

      var tmpParams = {};
      Object.assign(tmpParams, params);
      for (const property in tmpParams) {
        if(property !== 'active') {
          tmpParams[property] = {required: true};
        }
      }
      form.validate({
        rules: tmpParams
      });

      if (!form.valid()) {
        return;
      }

      Floading(btn[0]);
      FBlockLoading(form, lang('GLOBAL.SAVING'));

      var dataParam = {};
      Object.assign(dataParam, params);
      delete dataParam.element;
      delete dataParam.id;

      axios.put('api/update/'+params.id, dataParam).then(response => {
        if(response.data.success) {
          Fmessage(null, lang('GLOBAL.RECORD_UPDATED'), "success");
          setTimeout(function (e) {
            router.push("/Api");
          }, 1000);
        }else{
          Fmessage(null, response.data.message, "error");
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(btn[0]);
        FUnBlockLoading(form);
      });

    },
    deleteApi({commit, dispatch, state, rootState}, params){
      console.debug("api/deleteApi");

      var form = params.element;

      Floading(form.closest('tr'));
      FBlockLoading(form.closest('tr'), lang('GLOBAL.DELETING'));

      var dataParam = {};
      Object.assign(dataParam, params);
      delete dataParam.element;

      axios.delete('api/delete/'+params.id, dataParam).then(response => {
        if(response.data.success) {
          Fmessage(null, lang('GLOBAL.RECORD_DELETED'), "success");
          dispatch('getApiList');
        }else{
          Fmessage(null, response.data.message, "error");
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnloading(form.closest('tr'));
        FUnBlockLoading(form.closest('tr'));
      });

    },
    getPlatforms({commit, getters, rootState}) {
      console.debug("api/getPlatforms");
      FBlockLoading($('#kt_content'), null);
      axios.get('platform/list').then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setPlatforms", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    getPlatformFields({commit, getters, rootState}, params) {
      console.debug("api/getPlatformFields");
      FBlockLoading($('#kt_content'), null);
      axios.get('platform/fields/' + params.platformId).then(response => {
        if (response.data.success) {
          if(response.data.result !== undefined) {
            commit("setPlatformFields", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    getProducts({commit, getters, rootState}, params){
      console.debug("api/getProducts");
      FBlockLoading($('#kt_content'), null);

      var urlTmp = "product/getlistings/" + params.apiId;
      axios.get(urlTmp).then(response => {
        if (response.data.success) {
          if (response.data.result !== undefined) {
            commit("setProducts", response.data.result);
          }
        }
      }).catch(function (error) {
        axiosCatchMessage(error);
      }).then(function () {
        FUnBlockLoading($('#kt_content'), null);
      });
    },
    setApiProductToLocalProduct({commit, dispatch, state, rootState}, params){
      console.debug("api/setApiProductToLocalProduct");
      swal.fire({
        title: lang('API.PRODUCT_IMPORT_TITLE'),
        text: lang('API.PRODUCT_IMPORT_TEXT'),
        type: "info",
        showCancelButton: !0,
        confirmButtonText: lang('GLOBAL.YES'),
        cancelButtonText: lang('GLOBAL.NO'),
        reverseButtons: !0
      }).then(function (e) {
        if (e.value) {
          var productImportActionSwal = swal.fire({
            title: lang('API.PRODUCT_IMPORT_ACTION_TITLE'),
            text: lang('API.PRODUCT_IMPORT_ACTION_TEXT'),
            timer: 5000,
            onOpen: function() {
              swal.showLoading()
            }
          });
          var urlTmp = "product/importlistings/" + params.apiId;
          axios.get(urlTmp).then(response => {
            if (response.data.success) {
              if(response.data.result > 0){
                //var msg = sprintf(lang('API.PRODUCT_IMPORT_ACTION_SUCCESS'), {'amount': response.data.result});
                //Fmessage(null, msg, "success");
              }else{
                Fmessage(null, lang('API.PRODUCT_IMPORT_ACTION_NOT_FOUND'), "success");
              }
            }else{
              Fmessage(null, response.data.message, "error");
            }
          }).catch(function (error) {
            axiosCatchMessage(error);
          }).then(function () {
            productImportActionSwal.close();
          });
        }
      });
    },
  },
};

export default order



