import Vue from 'vue'
import axios from "axios";
import App from './App.vue'
import { router } from "./router";
import store from "./store/store";
import VueTheMask from 'vue-the-mask'

import { config } from "./config";
Vue.use(VueTheMask);
new Vue({
  el: '#app',
  data(){
    return {
      config: config,
      isAuth: false
    }
  },
  render: h => h(App),
  router,
  store,
  beforeCreate: function() {
  },
  created: function () {
    this.$data.isAuth = store.getters.isAuthenticated;
    axios.defaults.baseURL = this.$data.config.apiDomain;
    axios.defaults.withCredentials = true;
    store.getters.getConfig;
    store.getters.initConfig;
    if(this.$data.isAuth) {
      store.dispatch("user/getInfo");
    }
  }
});

import VueDragResize from 'vue-drag-resize'
Vue.component('vue-drag-resize', VueDragResize)

Vue.component("summernote", {
  template: "<textarea>{{value}}</textarea>",
  props: ["value"],
  data() {
    return {
      obj: ""
    };
  },
  mounted() {
    let vue = this;
    vue.obj = $(this.$el);
    let option = {
      height: 300,
    };
    option.callbacks = {
      onChange(contents) {
        vue.$emit("input", contents);
      }
    };
    vue.obj.summernote(option);
  }
});



